import { useState } from "react";

export default function Lecture({ lecture }){

    const [isDone, setIsDone] = useState(lecture.isDone);
    const inpId = lecture.id;

    function toggleDone(){
        //setIsDone(!isDone)
        fetch(`http://localhost:3001/lectures/${lecture.id}`, {
            method : 'PUT',
            headers : {
                'Content-type' : 'application/json', // Î≥¥ÎÇ¥Îäî Î¶¨ÏÜåÏä§Ïùò ÌÉÄÏûÖ
            },
            body: JSON.stringify({ // ÏàòÏ†ïÌïú Ï†ïÎ≥¥Î•º Ïã§Ïñ¥ÏÑú Î≥¥ÎÇ¥Í∏∞ ÏúÑÌï¥ Î∞îÎîîÏóê ÏûÖÎ†• /JSON.stringify- Î¨∏ÏûêÏó¥ Î∞òÌôòÏùÑ ÏúÑÌï¥
                ...lecture, // Ìï¥ÎãπÎç∞Ïù¥ÌÑ∞Ïóê
                isDone : !isDone // Ìï¥Îãπ ÎÇ¥Ïö© Ï†ÑÎã¨
            })
        })
        .then(res => { // ÏùëÎãµ Î∞õÏïÑÏÑú
            if( res.ok ){
                setIsDone(!isDone)
            }
        }
        );
    }

    return(
        <tr key={lecture.id} className={isDone ? 'off' : ''}>
            <td className="subject">{lecture.lecture}</td>
            <td className="finChk">
                <input type="checkbox" checked={isDone} onChange={toggleDone} id={inpId} />
                <label htmlFor={inpId}></label>
            </td>
            <td className="finDate">{lecture.findate}</td>
        </tr>
    );
}