import dummy from "../db/data.json"
import { useRef } from "react";
import { useHistory } from "react-router";

export default function CreateSkill(){
    const skillRef = useRef(null); // dom Ïóê Ï†ëÍ∑ºÌï† Ïàò ÏûàÍ≤å Ìï¥Ï§å
    const colorRef = useRef(null);
    const history = useHistory();
    
    function onSubmit(e){
        e.preventDefault();

        fetch(`http://localhost:3001/skills`, {
                method : 'POST',
                headers : {
                    'Content-type' : 'application/json', // Î≥¥ÎÇ¥Îäî Î¶¨ÏÜåÏä§Ïùò ÌÉÄÏûÖ
                },
                body: JSON.stringify({ 
                    skill: skillRef.current.value,
                    bgColor: colorRef.current.value
                })
            })
            .then(res => { // ÏùëÎãµ Î∞õÏïÑÏÑú
                if( res.ok ){
                    alert('ÏÉùÏÑ±Ïù¥ ÏôÑÎ£å ÎêòÏóàÏäµÎãàÎã§.');
                    history.push(`/skill/${skillRef.current.value}`)
                }
            }
        );
    }

    return(
        <main className="container createSkill">
            <section>
                <h3 className="cmTitle">‚ûï Skill Ï∂îÍ∞Ä</h3>
                <div className="skillListWrap">
                    <p className="formtagTit">ü§î ÌòÑÏû¨ ÎÇòÏùò Skill List</p>
                    <ul className="skillList">
                        {dummy.skills.map(skill => (
                            <li key={skill.id}>
                                <a style={{background: skill.bgColor }}> 
                                    {skill.skill} 
                                </a>
                            </li>
                        ))}
                    </ul>
                </div>

                <form onSubmit={onSubmit}>
                    <div className="formBox">
                        <label className="formtagTit">üìë Ï∂îÍ∞ÄÌï† Skill</label>
                        <input type="text" placeholder="Skill Ïù¥Î¶Ñ" ref={skillRef} />
                    </div>
                    <div className="formBox">
                        <label className="formtagTit">üìë Skill Color</label>
                        <input type="text" placeholder="#000000" ref={colorRef} />
                    </div>
                    <button className="btnSmt">Skill Ï†ÄÏû•</button>
                </form>
            </section>
        </main>
    );
}